import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../../shared/utility';

const initialState = {
  orders: [],
  loading: false,
  bought: false
}

const boughtInit = (state, action) => {
  return updateObject(state, {bought: false});
}

const boughtBurgerStart = (state, action) => {
  return updateObject(state, {loading: true});
}

const boughtBurgerSuccess = (state, action) => {
  const newOrder = updateObject(action.orderData, {id: action.orderId});
  return updateObject(state, {
    ...state,
    loading: false,
    bought: true,
    orders: state.orders.concat(newOrder)
  });
}

const boughtBurgerFailed = (state, action) => {
  return updateObject(state, {loading: false});
}

const fetchOrdersStart = (state, action) => {
  return updateObject(state, {loading: true});
}

const fetchOrdersSuccess = (state, action) => {
  return updateObject(state, {
    orders: action.orders,
    loading: false
  });
}

const fetchOrdersFail = (state, action) => {
  return updateObject(state, {loading: false});
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.BOUGHT_INIT: return boughtInit(state, action);
    case actionTypes.BOUGHT_BURGER_START: return boughtBurgerStart(state, action);
    case actionTypes.BOUGHT_BURGER_SUCCESS: return boughtBurgerSuccess(state, action);
    case actionTypes.BOUGHT_BURGER_FAILED: return boughtBurgerFailed(state, action);
    case actionTypes.FETCH_ORDERS_START: return fetchOrdersStart(state, action);
    case actionTypes.FETCH_ORDERS_SUCCESS: return fetchOrdersSuccess(state, action);
    case actionTypes.FETCH_ORDERS_FAIL: return fetchOrdersFail(state, action);
    default: return state;
  }
};

export default reducer;



